export default function getGridParent({
  focusState,
  focusedNode
}) {
  if (focusedNode.nodeNavigationItem === 'grid-item') {
    const rowNodeId = focusedNode.parentId;
    const rowNode = focusState.nodes[rowNodeId];

    if (!rowNode) {
      return null;
    }

    const gridNodeId = rowNode.parentId;
    const gridNode = focusState.nodes[gridNodeId];

    if (!gridNode) {
      return null;
    }

    return {
      gridNode,
      rowNode
    };
  } else if (focusedNode.nodeNavigationItem === 'grid-row') {
    const gridNodeId = focusedNode.parentId;
    const gridNode = focusState.nodes[gridNodeId];

    if (!gridNode) {
      return null;
    }

    return {
      gridNode,
      rowNode: focusedNode
    };
  }

  return null;
}