import { getParents, getChildren } from '../utils/tree-navigation';
import warning from '../utils/warning';

function generateFocusHierarchyFromId({
  focusState,
  propagateFromId,
  orientation,
  preferEnd
}) {
  const node = focusState.nodes[propagateFromId];
  let preferredChildren = [];

  if (node.trap) {
    preferredChildren = node._focusTrapPreviousHierarchy;
  }

  return [...getParents({
    focusState,
    nodeId: propagateFromId
  }), propagateFromId, ...getChildren({
    focusState,
    nodeId: propagateFromId,
    orientation,
    preferEnd,
    preferredChildren
  })];
}

export default function computeFocusHierarchy({
  focusState,
  assignFocusTo,
  orientation,
  preferEnd
}) {
  const explicitlyAssignFocus = typeof assignFocusTo === 'string' && assignFocusTo !== focusState.focusedNodeId;

  if (explicitlyAssignFocus) {
    // @ts-ignore
    const assignedNode = focusState.nodes[assignFocusTo];

    if (!assignedNode) {
      warning('You attempted to explicitly focus a node that was not found in the focus tree. ' + 'This may represent a bug in your application. ' + 'You should ensure that a node that matches onMountAssignFocusTo is created and not disabled. ' + 'This onMountAssignFocusTo value has been ignored; focus will be computed automatically.', 'EXPLICIT_FOCUS_ERROR');
    }

    const focusHierarchy = generateFocusHierarchyFromId({
      focusState,
      // @ts-ignore
      propagateFromId: assignFocusTo,
      orientation,
      preferEnd
    });
    return focusHierarchy;
  } else {
    return generateFocusHierarchyFromId({
      focusState,
      propagateFromId: focusState.focusedNodeId,
      orientation,
      preferEnd
    });
  }
}